.point::after {
  content: "";
  position: absolute;
  display: block;
  width: 85%;
  max-width: 50rem;
  height: 93.5%;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  background: linear-gradient(var(--g-angle), var(--g-color-0) var(--g-position-0), var(--g-color-1) var(--g-position-1));
  border-radius: 25px;
  --g-color-0: rgb(138, 213, 215);
  --g-position-0: 0%;
